import { keys } from "./js/keys";
import { success, error, info } from "@pnotify/core";
import Chart from "chart.js/auto";

const number = document.querySelector("#number");
const name = document.querySelector("#name");
const startBtn = document.querySelector("#start-game");
const restartBtn = document.querySelector("#restart-game");

let currentKeyIndex = 0;
let isPlaying = false;

const findCurrentKey = (index) => {
  if (index > 11 || !isPlaying) {
    success({
      title: "!",
      text: "  .",
    });
    name.textContent = ``;
    number.textContent = ``;
  } else {
    number.textContent = index + 1;
    name.textContent = `${keys[index].name}`;
  }
};

document.addEventListener("keydown", (e) => {
  e.preventDefault();
  if (!isPlaying) {
    return;
  }

  if (e.code === keys[currentKeyIndex].code) {
    currentKeyIndex += 1;
    findCurrentKey(currentKeyIndex);
    success({
      title: "!",
      text: "   .",
    });
  } else {
    error({
      title: " ...",
      text: "    .",
    });
  }
  console.log(currentKeyIndex);
});

startBtn.addEventListener("click", () => {
  info({
    title: "!",
    text: "  ",
  });
  isPlaying = true;
  findCurrentKey(currentKeyIndex);
});

restartBtn.addEventListener("click", () => {
  error({
    title: " !",
    text: "  ...",
  });
  isPlaying = false;
  currentKeyIndex = 0;
  findCurrentKey(currentKeyIndex);
});

